
.rank {
  min-height: 100vh;
  .headerBox {
    display: flex;
    justify-content: center;
    .header {
      width: 100%;
      display: flex;
      .banner {
        display: flex;
        height: 1.84rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .contentBox {
    display: flex;
    justify-content: center;
    .content {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      background: #fff;
      height: 5.64rem;
      border-radius: 26px 26px 0 0;
      margin: -25px 0 0;
      .menu {
        width: 100%;
        display: flex;
        justify-content: center;
        border-bottom: 1px solid #f6f6f6;
        .information {
          width: 60%;
          display: flex;
          justify-content: space-between;
          .watch,
          .give {
            display: flex;
            flex-direction: column;
            align-items: center;
            .title {
              font-size: 15px;
              font-family: PingFang SC-特粗, PingFang SC;
              font-weight: normal;
              color: #333333;
              line-height: 14px;
              margin: 21px 0 0;
              &.active {
                color: #ff2a00;
              }
            }
            .line {
              width: 0.24rem;
              height: 0.04rem;
              background: #ff2a00;
              border-radius: 2px;
              margin: 6px 0 9px;
            }
          }
        }
      }
      .table_header {
        width: 90%;
        display: flex;
        justify-content: space-between;
        .table_header_left {
          display: flex;
          .ranking {
            font-size: 14px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #323332;
            line-height: 14px;
            margin: 14px 0 12px;
          }
          .name {
            font-size: 14px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #323332;
            line-height: 14px;
            margin: 14px 0 12px 32px;
          }
        }
        .table_header_right {
          display: flex;
          .give_amount {
            font-size: 14px;
            font-family: PingFang SC-粗体, PingFang SC;
            font-weight: normal;
            color: #323332;
            line-height: 14px;
            margin: 14px 0 12px;
          }
        }
      }
      .table_content {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
        .container {
          width: 100%;
          display: flex;
          justify-content: center;
          background: #fafafa;
          &.active {
            background: #fff;
          }
          .information {
            width: 90%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 13px 0;
            .information-left {
              display: flex;
              .sequence {
                display: flex;
                img {
                  width: 30px;
                  height: 24px;
                }
                .sort {
                  width: 0.3rem;
                  height: 0.24rem;
                  text-align: center;
                  line-height: 24px;
                }
              }
              .info {
                display: flex;
                align-items: center;
                margin: 0 0 0 30px;
                img {
                  width: 22px;
                  height: 22px;
                  border-radius: 50%;
                  margin: 0 8px 0 0;
                }
                .user_name,
                .to,
                .phone_number {
                  font-size: 14px;
                  font-family: PingFang SC-中等, PingFang SC;
                  font-weight: normal;
                  color: #333333;
                  line-height: 14px;
                }
              }
            }
            .information-right {
              display: flex;
              .amount {
                width: 0.55rem;
                display: flex;
                .number {
                  font-size: 18px;
                  font-family: PingFang SC-粗体, PingFang SC;
                  font-weight: normal;
                  color: #333333;
                  line-height: 14px;
                }
              }
            }
          }
        }
      }
      .table_content::-webkit-scrollbar {
        display: none;
      }
    }
  }
}
